<template>
  <div class="w-100 m-auto form-signin">
    <div class="login-index">
      <h1 class="fw-bold">Đăng nhập hệ thống</h1>
      <div class="form-floating mb-2">
        <input
          type="text"
          class="form-control"
          id="floatingInput"
          v-model="username"
          placeholder="name@example.com"
        />
        <label for="floatingInput">Tài khoản đăng nhập</label>
      </div>
      <div class="form-floating">
        <input
          type="password"
          class="form-control"
          id="floatingPassword"
          placeholder="Password"
          v-model="password"
          v-on:keyup.enter="login"
        />
        <label for="floatingPassword">Mật khẩu</label>
      </div>
      <div>
        <button
          class="btn btn-primary w-100 mb-2"
          type="button"
          disabled
          v-if="isLoading"
        >
          <span
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          ></span>
          <span class="visually-hidden">Loading...</span>
        </button>
        <button class="btn btn-primary w-100 mb-2 fw-bold" v-if="!isLoading" @click="login()">
          Đăng nhập
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      username: "",
      password: "",
      isLoading: false,
      //   isLogin: false,
    };
  },
  watch: {
    isLogin(newValue) {
      this.isLoading = false;
      if (!this.currentPath) {
        this.$router.push({
          name: "dashboard",
        });
      } else {
        this.$router.push(this.currentPath);
      }
    },
  },
  created() {
   
    if (this.isLogin) this.$router.push(this.currentPath);
  },
  computed: {
    ...mapGetters("login", ["loginData", "isLogin", "currentPath"]),
  },
  methods: {
    // ...mapActions('login',['setLogin','helloWord']),
    login() {
      this.isLoading = true;
      const username = this.username;
      const password = this.password;

      if (this.username.length == 0 && this.password.length == 0) {
        // alert("Chưa nhập tài khoản hoặc mật khẩu");
        this.$toast.warning("Chưa nhập tài khoản hoặc mật khẩu");
        this.isLoading = false;
      } else this.$store.dispatch("login/setLogin", { username, password });

    },
  },
};
</script>

<style scoped>
.form-signin {
  max-width: 330px;
  padding: 15px;
}

.form-signin .form-floating:focus-within {
  z-index: 2;
}

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>
